.main {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #676767;
  }
  
  .container {
    position: fixed;
    z-index: 1000;
    width: 0 auto;
    bottom: 30px;
    margin: 0 auto;
    left: 30px;
    right: 30px;
    display: flex;
    flex-direction: 'column';
    pointer-events: none;
    align-items: flex-end;
  
    @media (max-width: 680px) {
      align-items: center;
    }
  }
  
  .message {
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
    width: 40ch;
  
    @media (max-width: 680px) {
      width: 100%;
    }
  }
  
  .content {
    opacity: 0.9;
    padding: 12px 22px;
    font-size: 1em;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10px;
    overflow: hidden;
    height: auto;
    border-radius: 3px;
    margin-top: 10px;
    margin-bottom: 0;
  }
  
  .button {
    cursor: pointer;
    pointer-events: all;
    outline: 0;
    border: none;
    background: transparent;
    display: flex;
    align-self: flex-end;
    overflow: hidden;
    margin: 0;
    padding: 0;
    padding-bottom: 22px !important;
    color: rgba(255, 255, 255, 0.5);
  }
  
  .button:hover {
    color: rgba(255, 255, 255, 0.6);
  }
  
  .life {
    position: absolute;
    bottom: 0;
    left: 0px;
    width: auto;
    background-image: linear-gradient(130deg, #00b4e6, #00f0e0);
    height: 5px;
  }
  